## Layout
*/
.root {
  overflow: hidden;
  padding-top: 0;
}

.wrapper {
  width: 100%;
  min-width: 100%;
}

.sp_container,
.mb_container,
.container {
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  padding-left: 30px;
  padding-right: 30px;
}
@media screen and (max-width: 575px) {
  .sp_container,
  .mb_container,
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media screen and (max-width: 420px) {
  .sp_container,
  .mb_container,
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.sp_container-nopadding,
.mb_container-nopadding,
.container-nopadding {
  padding-left: 0;
  padding-right: 0;
}

@media screen and (min-width: 768px) {
  .ipc_container {
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media screen and (min-width: 768px) and (max-width: 575px) {
  .ipc_container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media screen and (min-width: 768px) and (max-width: 420px) {
  .ipc_container {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media screen and (max-width: 767px) {
  .isp_container {
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media screen and (max-width: 767px) and (max-width: 575px) {
  .isp_container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media screen and (max-width: 767px) and (max-width: 420px) {
  .isp_container {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.sp_padding,
.padding {
  padding-left: 30px;
  padding-right: 30px;
}

.main_content img {
  display: block;
}

.sub_column {
  display: none;
}

.scroll_to_top {
  right: 0;
}

.sp_unindent {
  margin-left: -30px;
  margin-right: -30px;
}

.sp_cell + .sp_cell {
  margin-left: 1em;
}

/***********************************************************************
 ***********************************************************************
 ***********************************************************************

## Utility - Cosmetic

ユーティリティー設定。
このサイト専用というわけでもない共通設定。

<div class="_h3">.sp</div>
PCの時だけ表示される。

<div class="_h3">.sp_block</div>
SPの時だけ、ブロック要素になる。

<div class="_h3">.tel_link</div>
SPの時だけ、リンクになる。(クリックで電話するリンク)

*/
.sp_none,
.root .sp_none {
  display: none;
}

.block,
.sp_block {
  display: block;
}

.xs_block {
  display: none;
}

@media screen and (max-width: 575px) {
  .xs_none {
    display: none;
  }
  .xs_block {
    display: block;
  }
}
@media screen and (min-width: 768px) {
  .ipc_block {
    display: block;
  }
}
@media screen and (max-width: 767px) {
  .isp_block {
    display: block;
  }
  .ipc_block + .ipc_block {
    margin-left: 0.7em;
  }
}
.sp_click {
  cursor: pointer;
}

.scell {
  display: block;
  margin-right: 0;
}

.tel_link-no {
  text-decoration: none;
  pointer-events: none;
  color: inherit;
}

