## Component - Base

コンポーネント設定。
各ページでよく使い回す記述。
このサイト専用の共通設定。

*/
/**************************

## - 段組設定 2列 3列 4列

SPの時だけ、中央に等間隔で余白を作り、width: 100%; を守って列になる。
ul でなくてもいい。

**2列 .sp_column2**

	<ul class="sp_column2">
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
	</ul>

**3列 .sp_column3**

	<ul class="sp_column3">
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
	</ul>

**4列 .sp_column4**

	<ul class="sp_column4">
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
	</ul>

*/
.sp_column2 {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  margin-bottom: -10px;
  width: calc(100% + 10px);
}
.sp_column2 > * {
  padding-left: 5px;
  padding-right: 5px;
  box-sizing: border-box;
  width: 50%;
  margin-bottom: 10px;
}

.sp_column3 {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  margin-bottom: -10px;
  width: calc(100% + 10px);
}
.sp_column3 > * {
  padding-left: 5px;
  padding-right: 5px;
  box-sizing: border-box;
  width: 33.33%;
  margin-bottom: 10px;
}

.sp_column4 {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  margin-bottom: -10px;
  width: calc(100% + 10px);
}
.sp_column4 > * {
  padding-left: 5px;
  padding-right: 5px;
  box-sizing: border-box;
  width: 25%;
  margin-bottom: 10px;
}

.sp_column2 img,
.sp_column3 img,
.sp_column4 img {
  margin-left: auto;
  margin-right: auto;
}

.pc_column2 img,
.pc_column3 img,
.pc_column4 img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  vertical-align: bottom;
}

/**************************
.pc_column
*/
.pc_column2:not(.sp_column2, .sp_column3, .sp_column4) > *:not(:last-child) {
  margin-bottom: 1em;
}

.pc_column3:not(.sp_column2, .sp_column3, .sp_column4) > *:not(:last-child) {
  margin-bottom: 1em;
}

.pc_column4:not(.sp_column2, .sp_column3, .sp_column4) > *:not(:last-child) {
  margin-bottom: 1em;
}

.pc_column2b:not(.sp_column2, .sp_column3, .sp_column4) > *:not(:last-child) {
  margin-bottom: 1em;
}

.pc_column3b:not(.sp_column2, .sp_column3, .sp_column4) > *:not(:last-child) {
  margin-bottom: 1em;
}

.pc_column4b:not(.sp_column2, .sp_column3, .sp_column4) > *:not(:last-child) {
