/**************************
	.table-column2
*/
.table-column2 {
  width: 100% !important;
  max-width: 100% !important;
}
.table-column2 thead,
.table-column2 tbody,
.table-column2 tfoot,
.table-column2 tr,
.table-column2 th,
.table-column2 td {
  display: block;
  box-sizing: border-box;
  float: left;
  clear: both;
}
.root .table-column2 thead,
.root .table-column2 tbody,
.root .table-column2 tfoot,
.root .table-column2 tr,
.root .table-column2 th,
.root .table-column2 td {
  width: 100%;
}
.table-column2 tr th,
.table-column2 th + td {
  border-top: 0 none;
}
.table-column2 thead th,
.table-column2 thead td,
.table-column2 tbody th,
.table-column2 tbody td {
  width: 100% !important;
}
.table-column2 thead td + td,
.table-column2 tbody td + td {
  border-top: 0 none;
}
.table.table-column2:not(.table-noborder) tr:first-child th:first-child,
.table.table-column2:not(.table-noborder) tr:first-child td:first-child {
  border-top: 1px solid #d4d2d0;
}

/* form */
