## Project

特定ページ専用、効果範囲が限定的な記述。
コンテンツ内容に密接で、汎用性がなく、機能の付け替えを想定していない。

*/
.open_btn {
  text-indent: -119988px;
  overflow: hidden;
  text-align: left;
  /* stylelint-disable-next-line at-rule-empty-line-before */
  margin: 0;
  width: 30px;
  height: 30px;
  display: block;
  position: fixed;
  right: 15px;
  padding: 10px;
  background-color: #66acac;
}
.open_btn .hamburger_btn__inner {
  display: block;
  box-sizing: border-box;
  position: relative;
  width: 30px;
  height: 30px;
  top: 50%;
  transform: translateY(-50%);
  transition: top 0.3s cubic-bezier(0.23, 1, 0.32, 1);
}
.open_btn .hamburger_btn__inner > span {
  display: block;
  height: 2px;
  width: 30px;
  border-radius: 1px;
  background-color: #f9fafa;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: top 0.3s cubic-bezier(0.23, 1, 0.32, 1), transform 0.5s cubic-bezier(0.23, 1, 0.32, 1);
}
.open_btn .hamburger_btn__inner > span:nth-child(1) {
  top: 5px;
}
.open_btn .hamburger_btn__inner > span:nth-child(2) {
  top: 15px;
}
.open_btn .hamburger_btn__inner > span:nth-child(3) {
  top: 25px;
}
.open_btn:hover {
  opacity: 0.7;
}

.close_btn {
  text-indent: -119988px;
  overflow: hidden;
  text-align: left;
  /* stylelint-disable-next-line at-rule-empty-line-before */
  margin: 0;
  width: 24px;
  height: 24px;
  position: fixed;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  box-sizing: border-box;
  z-index: 2;
  display: none;
}
.close_btn::before {
  content: "";
  display: block;
  background: url("../img/sp/menu_close.webp") 0 0 no-repeat transparent;
  background-size: 24px 24px;
  width: 24px;
  height: 24px;
}
.close_btn:hover {
  opacity: 0.7;
}